.check-transparent label,
.check-circle label,
.check-star label {
    display: inline-block;
}

.check-transparent input,
.check-circle input,
.check-star input {
    display: none;
}

.check-circle label {
    height: 2.3em;
    width: 2.8em;
    margin: -0.39em 0.74em;
}

.check-circle label:before {
    content: '';
    cursor: pointer;
    height: 1.95em;
    width: 2.42em;
    display: inline-block;
    border: 0.17em solid #d82;
    border-radius: 3em;
    background: #ec5;
}

.check-circle :checked+label:hover:before {
    background: #a84;
}

.check-circle :checked+label:before {
    background: #a62;
}

.check-circle label:hover:before {
    background: #ed4;
}

.check-star label {
    height: 1.027em;
    width: 1.027em;
    margin: 0 0.205em;
    zoom: 1.35;
}

.check-star label:before {
    content: '';
    cursor: pointer;
    height: 1.053em;
    width: 1.31em;
    display: inline-block;
    background-size: contain;
    background-image: url('/star.empty.png');
    margin: -0.0513em -0.154em;
}

.check-star :checked+label:hover:before {
    background-image: url('/star.hover.full.png');
}

.check-star :checked+label:before {
    background-image: url('/star.full.png');
}

.check-star label:hover:before {
    background-image: url('/star.hover.png');
}

.check-transparent label:before {
    content: '';
    cursor: pointer;
    vertical-align: bottom;
    color: black;
    background-color: rgba(0, 0, 0, 0.01);
    line-height: 1.1em;
    font-weight: bold;
    text-align: center;
    font-size: 1.027em;
    height: 1.027em;
    width: 1.027em;
    display: inline-block;
}

.check-transparent :checked+label:before {
    content: '\2713';
    color: #000;
    text-shadow: 0px 0px 3px #fff, 0px 0px 3px #fff;
    margin: 0.1em 0;
}

.check-transparent label:hover:before {
    color: #a53;
}

